
@keyframes darken-background {
    to {
        opacity: .7;
    }
}


.background {
    z-index: -10;
    position: fixed;
    width: 100%;
    height: 100%;
    background-image: url("./../../assets/video/ark.gif");
    background-repeat: no-repeat;
    background-size: 100% auto;

    background-position: 0 33%;

    animation: 5s ease 2s 1 darken-background forwards;
    
}

